/*-----------------------------------------------------------------------------------*/

/* CHECKBOX */

/*-----------------------------------------------------------------------------------*/

input[type="checkbox"]{
    display:none;
}

.checkbox label div{
    width: 20px;
    height: 20px;
    display: inline-block;
    border: 1px solid #ef3b29;
    text-align: center;
    line-height: 20px;
    margin-right: 6px;
    margin-left: -20px;
    border-radius: 3px;
    cursor: pointer;
}

.checkbox label i{
    font-size: 16px;
    opacity: 0;
    color: #fff;
}

.checkbox input:checked+label div {
  background-color: #ef3b29;
}

.checkbox input:checked+label i{
    opacity: 1;
}

.checkbox input:disabled+label div {
    background-color: #dedede;
    border-color: #606060;
}

/*-----------------------------------------------------------------------------------*/

/* RADIO BUTTON */

/*-----------------------------------------------------------------------------------*/

[type="radio"]:checked,
[type="radio"]:not(:checked) {
    position: absolute;
    left: -9999px;
}

[type="radio"]:checked + label,
[type="radio"]:not(:checked) + label
{
    position: relative;
    padding-left: 20px;
    cursor: pointer;
    line-height: 20px;
    display: inline-block;
    color: #606060;
    font-size: 13px;
}

[type="radio"]:checked + label:before,
[type="radio"]:not(:checked) + label:before {
    content: '';
    position: absolute;
    left: 0;
    top: 3px;
    width: 14px;
    height: 14px;
    border: 1px solid #979797;
    border-radius: 100%;
    background: #fff;
}

[type="radio"]:checked + label:after,
[type="radio"]:not(:checked) + label:after {
    content: '';
    width: 8px;
    height: 8px;
    background: #ef3b29;
    position: absolute;
    top: 6px;
    left: 3px;
    border-radius: 100%;
    -webkit-transition: all 0.2s ease;
    transition: all 0.2s ease;
}

[type="radio"]:not(:checked) + label:after {
    opacity: 0;
    -webkit-transform: scale(0);
    transform: scale(0);
}

[type="radio"]:checked + label:after {
    opacity: 1;
    -webkit-transform: scale(1);
    transform: scale(1);
}

[type="radio"]:disabled + label{
    cursor: not-allowed;
}

/*-----------------------------------------------------------------------------------*/

/* RADIO BUTTON VERSION 2*/

/*-----------------------------------------------------------------------------------*/

.radio-group input[type=radio] {
    position: absolute;
    visibility: hidden;
    display: none;
}

.radio-group label {
    color: #ef3b29;
    display: inline-block;
    cursor: pointer;
    font-weight: bold;
    padding: 9px 20px;
    width: 100%;
    border: solid 1px #ef3b29;
}

.radio-group label::before{
    content: none !important;
}

.radio-group label::after{
    content: none !important;
}

.radio-group input[type=radio]:checked + label {
    color: #fff;
    background: #ef3b29;
    border: solid 1px #ef3b29;
}

.radio-group label + input[type=radio] + label{
    border: solid 1px #ef3b29;
}

.radio-group {
    overflow: hidden;
}

